import { NgModule } from '@angular/core';
import {
  MatButtonModule,
  MatCardModule,
  MatCheckboxModule,

  MatDialogModule,

  MatIconModule,
  MatInputModule,

  MatMenuModule,

  MatSnackBarModule,
  MatToolbarModule,
} from "@angular/material";

@NgModule({
  exports: [

    MatButtonModule,

    MatCardModule,
    MatCheckboxModule,

    MatDialogModule,

    MatIconModule,
    MatInputModule,

    MatMenuModule,

    MatSnackBarModule,

    MatToolbarModule,

  ],
  declarations: []
})
export class MaterialModule {}



